import React from 'react'
import {Link} from 'react-router'

class HomePage extends React.Component {
  componentDidMount() {}
  render() {
    return (
      <div className="jumbotron">
        <h1>Etudiant Entrepreneur</h1>
        <p>Le statut national d'tudiant-entrepreneur permet aux tudiant(e)s et aux jeunes diplm(e)s d'laborer un projet entrepreneurial dans un PEPITE. Le diplme d'tablissement "tudiant-entrepreneur" (D2E) accompagne le statut d'tudiant-entrepreneur : il permet de mener  bien son projet avec un maximum de scurit et de visibilit.</p>
        <Link to="application" className="btn btn-primary btn-lg">Demandez le statut</Link>
      </div>
    )
  }
}

export default HomePage



